@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movieRow{
    margin-bottom: 30px;
    
}

.movieRow h2{
    margin: 0px 0px 0px 30px;

}

.movieRow-listA{
    overflow-x: hidden; /*tudo que passar da tela na horizontal(x) vai ser cortado*/
    padding-left: 30px;
}

.movieRow-list{
    transition: all ease 0.5s;
    cursor: pointer;
}

.movieRow-item{
    display: inline-block;
    width: 150px;
}
.movieRow-item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease 0.2s; /*da uma transição de 0.2 segundos para o efeito de escala*/
}
.movieRow-item img:hover{  /*ao passar o mouse em cima da imagem a escala aumenta para 1*/
    -webkit-transform: scale(1);
            transform: scale(1);
}

.movieRow-left,
.movieRow-right{
    position: absolute;
    width: 40px;
    height: 225px;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease 0.5s;
}
.movieRow-left{
    left: 0;
}
.movieRow-right{
    right: 0;
}
.movieRow:hover .movieRow-left,
.movieRow:hover .movieRow-right{
    opacity: 1;
}

@media (max-width:760px){
    .movieRow-left,
    .movieRow-right{
        opacity: 1;
    }
}

.featured{
    height: 100vh; /*define a altura do tamanho da tela do usuario*/
    
}


.featured-vertical{
    width: inherit;  /*acompanha a largura da anterior*/
    height: inherit; /*acompanha a altura da anterior*/
    background: linear-gradient(to top, #111 10%, transparent 90%); /*da o tom de degrade ao backdrop de cima para baixo*/
}
.featured-horizontal{
    width: inherit;  /*acompanha a largura da anterior*/
    height: inherit; /*acompanha a altura da anterior*/
    background: linear-gradient(to right, #111 25%, transparent 75%); /*da o tom de degrade ao backdrop da esquerda para direita*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 80px;
    padding-top: 20px;
}


.featured-name{
    font-size: 60px;
    font-weight: bold;

}


.featured-infos{
    display: flex;
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;  
}
.featured-points{
    color: #46d369;
}
.featured-points, 
.featured-year, 
.featured-seasons{
    margin-right: 15px;
}


.featured-description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    max-width: 40%; /*largura maxima da tela 40% da tela*/
    overflow: hidden;
    text-overflow: ellipsis; /* Adicionando "..." ao final */
    display: -webkit-box;
    -webkit-line-clamp: 5; /* Quantidade de linhas */
    -webkit-box-orient: vertical;
}


.featured-btns{
    margin-top: 15px;
    display: flex;
}
.featured-Wbtn{
    background-color: #fff;
    color: #000;
}
.featured-Lbtn{
    background-color: #333;
    color: #fff;
}
.featured-Wbtn, 
.featured-Lbtn{
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}
.featured-Wbtn:hover, 
.featured-Lbtn:hover{
    opacity: 0.7;
}


.featured-genres{
    margin-top: 15px;
    font-size: 18px;
    color: #fff;
}

@media (max-width:760px){
    .featured{
        height: 90vh;
    }
    .featured-name{
        font-size: 40px;
    }
    .featured-infos{
        font-size: 16px;
    }
    .featured-description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured-Lbtn,
    .featured-Wbtn{
        font-size: 16px;
    }
    .featured-genres{
        font-size: 14px;
    }
}
header{
   position:fixed;
   z-index: 999;
   top: 0;
   left: 0;
   right: 0;
   height: 70px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 0 30px;
   background: transparent;
   transition: all ease 0.5s;
}
header.transition{
   background-color: #141414;
}

.header-logo{
    height: 25px;
}
.header-logo img{
    height: 100%;
}
.header-user{
    height: 35px;
}
.header-user img{
    height: 100%;
    border-radius: 3px;
}
footer{
    margin: 50px 0;
    text-align: center;
}
footer a{
   
    color:#fff
}
*{
  box-sizing: border-box;
}

body{
  background-color: #111;
  color: #FFF;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.list{
  margin-top: -80px;
}

.loading{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background-color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}
